*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Poppins';
    background: rgb(70,70,70);
}
body{
    overflow-x: hidden;
}

.webgl
{
    position: absolute;
    top: 0;
    left: 0;
    outline: none;
    cursor : crosshair;
    /*mix-blend-mode : overlay;*/
}

.container{
    height: 100vh;
    display: grid;
    place-items: center;
    position: relative;
    z-index: 1;
    text-align: center;
}
h1{
    font-size: 8rem;
    text-transform: uppercase;
    color: white;
}
section{
    height: 100vh;
}